<script>
import VClamp from './components/Clamp'
import qs from 'qs'
import hljs from 'highlight.js/lib/highlight.js'
import vue from './vue-lang.js'
import javascript from 'highlight.js/lib/languages/javascript'
import diff from 'highlight.js/lib/languages/diff'
import shell from 'highlight.js/lib/languages/shell'

hljs.registerLanguage('vue', vue)
hljs.registerLanguage('javascript', javascript)
hljs.registerLanguage('diff', diff)
hljs.registerLanguage('shell', shell)

const search = location.search.replace(/^\?/, '')
const query = qs.parse(search)
const zh = query.lang === 'zh'

export default {
  name: 'app',
  components: {
    VClamp
  },
  data () {
    return {
      lines0: 3,
      width0: 100,
      lines1: 3,
      width1: 100,
      hyphens1: true,
      rtl1: false,
      expanded1: false,
      height: 'calc(32px + 7em)',
      width2: 100,
      hyphens2: true,
      rtl2: false,
      lines3: 5,
      width3: 100,
      hyphens3: true,
      rtl3: false,
      clamped3: false,
      text:
        'Vue (pronounced /vju/, like view) is a progressive framework for building user interfaces. Unlike other monolithic frameworks, Vue is designed from the ground up to be incrementally adoptable. The core library is focused on the view layer only, and is easy to pick up and integrate with other libraries or existing projects. On the other hand, Vue is also perfectly capable of powering sophisticated Single-Page Applications when used in combination with modern tooling and supporting libraries.',
      textZh:
        'Vue ( /vju/ view) Vue Vue Vue ',
      zh,
      pascal: false
    }
  },
  computed: {
    defaultText () {
      return this.zh ? '' : 'Default:'
    },
    parameterText () {
      return this.zh ? '' : 'Callback parameter list:'
    }
  },
  watch: {
    zh (val) {
      if (val) {
        query.lang = 'zh'
      } else {
        delete query.lang
      }
      let search = qs.stringify(query)
      search = search ? `?${search}` : search
      history.pushState(
        null,
        null,
        `${location.origin}${location.pathname}${search}${location.hash}`
      )
    }
  },
  mounted () {
    ;[...this.$el.querySelectorAll('pre.code')].forEach(code => {
      hljs.highlightBlock(code)
    })
  }
}
</script>

